
import XImg from 'vux/src/components/x-img/index.vue'

import {exchangeLog} from '../config'
export default {
  head: {
    title: {
      inner: '兑换记录'
    }
  },
  data () {
    return {
      height: '',
      loading: false,
      list: [],
      form: {
        userId: 0,
        limit: 5,
        pageIndex: 0
      }
    }
  },
  components: {
    XImg
  },
  mounted () {
    this.form.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
    this.getData()
    this.top = 1
    this.bottom = this.form.pageIndex
  },
  created () {
  },
  methods: {
    loadMore (done) {
      if (this.bottom >= 30) {
        setTimeout(() => {
          done(true)
        }, 1500)
        return
      }
      setTimeout(() => {
        let start = this.bottom + 1
        console.log(start)
        for (let i = start; i < start + this.form.limit; i++) {
          this.list.push(i + ' - keep walking, be 2 with you.')
        }
        this.bottom = this.bottom + this.form.limit
        setTimeout(() => {
          done()
        })
      }, 1500)
    },
    getData () {
      this.$http({
        method: 'jsonp',
        url: exchangeLog,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form,
        before: () => {
          this.loading = true
        }
      })
      .then(res => {
        console.log(res)
        this.list = res.body.data.productList
        this.loading = false
      })
    }
  }
}



